import { create, StateCreator } from 'zustand'

interface VaultsAddState {
  amountInputA: string
  amountInputB: string
  setAmountInputA: (amountInputA: string) => void
  setAmountInputB: (amountInputB: string) => void
  pythPriceMap: Record<string, any>
  setPythPriceMap: (priceMap: Record<string, any>) => void
}

const store: StateCreator<VaultsAddState> = (set, get) => ({
  amountInputA: '',
  amountInputB: '',
  setAmountInputA: (value: string) => set({ amountInputA: value }),
  setAmountInputB: (value: string) => set({ amountInputB: value }),
  pythPriceMap: {},
  setPythPriceMap: (priceMap: Record<string, any>) => set(() => ({ pythPriceMap: priceMap }))
})

const useVaultsAddStore = create(store)
export default useVaultsAddStore
