import { create, StateCreator } from 'zustand'

interface VaultsDetailState {
  vaultsCoinPercent: any
  setVaultsCoinPercent: (vaultsCoinPercent: any) => void
  vaultsCoinAValue: string
  vaultsCoinBValue: string
  setVaultsCoinAValue: (vaultsCoinAValue: string) => void
  setVaultsCoinBValue: (vaultsCoinBValue: string) => void
  vaultCoinAmounts: {
    coinAmountA: string
    coinAmountB: string
    displayCoinA: string
    displayCoinB: string
  }
  setVaultCoinAmounts: (vaultCoinAmounts: { coinAmountA: string; coinAmountB: string; displayCoinA: string; displayCoinB: string }) => void
}

const store: StateCreator<VaultsDetailState> = (set, get) => ({
  vaultsCoinPercent: {},
  setVaultsCoinPercent: (vaultsCoinPercent: any) => set(() => ({ vaultsCoinPercent })),
  vaultsCoinAValue: '',
  vaultsCoinBValue: '',
  setVaultsCoinAValue: (vaultsCoinAValue: string) => set(() => ({ vaultsCoinAValue })),
  setVaultsCoinBValue: (vaultsCoinBValue: string) => set(() => ({ vaultsCoinBValue })),
  vaultCoinAmounts: {
    coinAmountA: '',
    coinAmountB: '',
    displayCoinA: '',
    displayCoinB: ''
  },
  setVaultCoinAmounts: (vaultCoinAmounts: { coinAmountA: string; coinAmountB: string; displayCoinA: string; displayCoinB: string }) =>
    set(() => ({ vaultCoinAmounts }))
})

const useVaultsDetailStore = create(store)
export default useVaultsDetailStore
